body {
  background: linear-gradient(45deg, #eecfba, #c5dde8);
}

.cat-info {
  flex-direction: column;
  margin-top: 10px;
  display: flex;
}

.cat-img {
  border-radius: 10px;
  display: block;
}

.cat-description {
  max-width: 350px;
  background-color: #fff;
  border-radius: 10px;
  margin-top: 10px;
  padding: 20px;
}

.hidden {
  opacity: 0;
  display: none;
}

.breed-select {
  width: 200px;
  height: 30px;
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  font-size: 14px;
}

.breed-select:hover {
  border-color: #999;
}

.breed-select:focus {
  outline: none;
  box-shadow: 0 0 5px #999;
}

.breed-select option {
  padding: 5px;
}

.loader {
  width: 24px;
  height: 24px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

